.header {
  box-shadow: 0px 0px 10px rgba(151, 151, 151, 0.16);
  background-color: white;
  &.dark {
    background-color: #2b3743;
  }

  & .header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 20px;

    & .title {
      font-weight: 800;
      font-size: 32px;
      @media (max-width: 600px) {
        font-size: 26px;
      }
    }
  }
}
