.search {
  display: flex;
  align-items: center;
  column-gap: 20px;
  min-width: 35%;
  padding: 12px 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(151, 151, 151, 0.16);
  background-color: white;
  color: #757575;
  @media (max-width: 600px) {
    width: 100%;
  }

  &.dark {
    background-color: #2b3743;
  }

  & svg {
    transform: scale(0.8);
    cursor: pointer;
    transition: color 0.3s ease-in-out;

    &:hover {
      color: black;
      &.dark {
        background-color: white;
      }
    }
  }

  & .input__search {
    flex-grow: 1;
    border: none;
    outline: 0;
  }
}
